const months = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    ""
]

function getDateDDMMYYYY(date: Date, needYear = true) {
    if (!needYear) return `${date.getDate()} ${months[date.getMonth()]}`;

    return `${date.getDate()} ${months[date.getMonth()]} ${date.getFullYear()}`;
}

export {getDateDDMMYYYY};
